<template>
  <div class="mark-down">
    <MarkdownPreview v-if="markdown" :initialValue="markdown" />
    <div id="markdown-gitalk"></div>
  </div>
</template>

<script>
import { MarkdownPreview } from "vue-meditor";
export default {
  components: {
    MarkdownPreview
  },
  data() {
    return {
      markdown: null,
      gitalk: null
    };
  },
  watch: {
    "$route.query": {
      handler({ type, url, key }) {
        let value;
        if (type) {
          switch (type) {
            case "@/zist":
              value = require(`@/zist/${url}`);
              break;
          }
        }
        this.markdown = value;
      },
      immediate: true
    }
  },
  mounted() {},
  beforeDestroy() {
    this.markdown = null;
  }
};
</script>

<style lang="scss" scoped>
.mark-down {
  width: 100%;
  height: 100%;
}
</style>